import React, { Component, Fragment } from 'react';
import { Row, Col, Form, Select, Button, DatePicker, Input, Checkbox, Radio } from 'antd';
import moment from 'moment';
import FilterPathEducate from 'components/common/input/FilterPathEducate';

const { Option } = Select;

const dateFormat = 'YYYY/MM/DD';

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  toggle = () => {
    const { expand } = this.state;
    this.setState({ expand: !expand });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} classes={{ isShow: false }} />
          <Col span={8}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('system5', {
                rules: [
                  {
                    required: true,
                    message: 'Input something!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Năm học">
              {getFieldDecorator('system6', {
                rules: [
                  {
                    required: true,
                    message: 'Input something!',
                  },
                ],
              })(
                <Fragment>
                  <DatePicker defaultValue={moment('2015/01/01', dateFormat)} format={dateFormat} />
                </Fragment>
              )}
            </Form.Item>
          </Col>

          <Col span={8}>
            <Form.Item label="Xuất sắc">{getFieldDecorator('system11', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Giỏi">{getFieldDecorator('system12', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Khá">{getFieldDecorator('system13', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Phương án xét">
              {getFieldDecorator('system7', {
                rules: [
                  {
                    required: true,
                    message: 'Input something!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Xét theo số xuất">
              {getFieldDecorator('system8', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Xét theo % sinh viên được học bổng">
              {getFieldDecorator('system9', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Xét theo số tiền">
              {getFieldDecorator('system10', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system14', {})(<Checkbox>Không tính điểm môn học lại </Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system15', {})(<Checkbox>Lấy hơn </Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system16', {})(<Checkbox>Lấy kém </Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system19', {})(<Checkbox> Chọn tất cả </Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('system17', {})(
                <Radio>Xem danh sách sinh viên chưa nhận được học bổng</Radio>
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('system18', {})(
                <Radio>Xem danh sách sinh viên đã nhận được học bổng</Radio>
              )}
            </Form.Item>
          </Col>

          <Col span={8}>Số tiền đã xét : 10</Col>
          <Col span={8}>Số sv được học bổng : 10</Col>
          <Col span={8}>% sv được học bổng : 8</Col>
          <Col span={8}>Số sinh viên : 8</Col>
          <Col span={8}>Số tiền đã xét : 8</Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit">
              Liệt kê danh sách
            </Button>
            <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>
              Xóa
            </Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



