import React, { Component } from 'react';
import { Form, Select, Col } from 'antd';

const { Option } = Select;
export default class FacultyInput extends Component {
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Col span={8}>
        <Form.Item label="Khoa">
          {getFieldDecorator('faculty', {})(
            <Select>
              <Option value="1">Cấp trường</Option>
              <Option value="2">Cấp tỉnh</Option>
            </Select>
          )}
        </Form.Item>
      </Col>
    );
  }
}



