import React, { Component } from 'react';
import { Form, Select, Col } from 'antd';

const { Option } = Select;
export default class ClassInput extends Component {
  render() {
    const { getFieldDecorator } = this.props.form;
    const { isShow } = this.props;
    return isShow ? (
      <Col span={8}>
        <Form.Item label="Lớp">
          {getFieldDecorator('class', {})(
            <Select>
              <Option value="1">Cấp trường</Option>
              <Option value="2">Cấp tỉnh</Option>
            </Select>
          )}
        </Form.Item>
      </Col>
    ) : null;
  }
}

ClassInput.defaultProps = {
  isShow: true,
};



